.gradient-bkg {
  background: linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
  border-radius: 0px 0px 12px 12px;
  padding-bottom: 60px;
  position: relative;
}

.gradient-bkg::after {
  content: '';
  position: absolute;
  width: calc(100% - 40px);
  height: 10px;
  background: #CACADB;
  left: 20px;
  bottom: -10px;
  border-radius: 0 0 12px 12px;
}

.gradient-bkg::before {
  content: '';
  position: absolute;
  width: calc(100% - 80px);
  height: 10px;
  background: #E7E7F2;
  left: 40px;
  bottom: -20px;
  border-radius: 0 0 12px 12px;
}

.bkg-img {
  background-image: url(../img/h-bkg.svg);
  background-position: top center;
  background-repeat: no-repeat;
}

@media (min-width: 768px) {
